import {
  IonAlert,
  IonApp,
  IonCol,
  IonContent,
  IonGrid,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonRow,
  IonTitle,
  IonToolbar,
} from "@ionic/react";

import React, { useRef, useState } from "react";
import EncryptionControls from "./components/EncryptionControls";

import Encryption from "../src/plugins/EncryptionPlugin";

/* Core CSS required for Ionic components to work properly */
import "@ionic/react/css/core.css";

/* Basic CSS for apps built with Ionic */
import "@ionic/react/css/normalize.css";
import "@ionic/react/css/structure.css";
import "@ionic/react/css/typography.css";

/* Optional CSS utils that can be commented out */
import "@ionic/react/css/padding.css";
import "@ionic/react/css/float-elements.css";
import "@ionic/react/css/text-alignment.css";
import "@ionic/react/css/text-transformation.css";
import "@ionic/react/css/flex-utils.css";
import "@ionic/react/css/display.css";

/* Theme variables */
import "./theme/variables.css";
import EncryptedResult from "./components/EncryptedResult";

const App: React.FC = () => {
  const [encryptedText, setEncryptedText] = useState<string>();
  const [error, setError] = useState<string>();

  const plainTextInputRef = useRef<HTMLIonInputElement>(null);

  const encryptText = async () => {
    const enteredPlainText = plainTextInputRef.current?.value;

    if (!enteredPlainText) {
      setError("Uh oh");
      return;
    }

    const { encryptedText } = await Encryption.encrypt({
      plainText: enteredPlainText.toString(),
    });
    //const bmi = +enteredWeight / (+enteredHeight * +enteredHeight);
    setEncryptedText(encryptedText);
    //console.log(bmi);
  };

  const clearError = () => {
    setError("");
  };

  return (
    <React.Fragment>
      <IonAlert
        isOpen={!!error}
        message={error}
        buttons={[
          {
            text: "Okay",
            handler: clearError,
          },
        ]}
      />
      <IonApp>
        <IonHeader>
          <IonToolbar color="primary">
            <IonTitle>RSA Encryption Tools</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent className="ion-padding">
          <IonGrid>
            <IonRow>
              <IonCol>
                <IonItem>
                  <IonLabel position="floating">Your String</IonLabel>
                  <IonInput ref={plainTextInputRef}></IonInput>
                </IonItem>
              </IonCol>
            </IonRow>
            <EncryptionControls onEncrypt={encryptText} />
            {encryptedText && <EncryptedResult result={encryptedText} />}
          </IonGrid>
        </IonContent>
      </IonApp>
    </React.Fragment>
  );
};

export default App;
